import { Document, Text } from '@ali/4ever-cangjie';
import { Paragraph } from '@ali/4ever-plugin-paragraph';

export default function canCopyOrCut(fragment: Document) {
  const firstChild = fragment.nodes[0];
  return !(
    fragment.nodes.length === 1 &&
    Paragraph.isParagraph(firstChild) &&
    Text.isTextList(firstChild.nodes) &&
    firstChild.text === ''
  );
}
